.groupEdit input,
.groupEdit textarea {
    border-radius: 20px;
    border: 1px solid transparent;
    display: block;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: auto;
    margin-left: auto;
    padding: 10px;
    width: 300px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}

.groupEdit textarea {
    height: 100px;
    resize: none;
}

.groupEdit button {
    width: 100px;
    border-radius: 20px;
    border: 1px solid black;
    transition: 0.8s;
    -webkit-transition: 0.8s;
    -moz-transition: 0.8s;
    -ms-transition: 0.8s;
    -o-transition: 0.8s;
}

.groupEdit button:hover {
    background: black;
    color: white;

}

@media (max-width:390px) {

    .groupEdit input,
    .groupEdit textarea {
        width: 200px;
    }

    .groupEdit button {
        width: 200px;
    }
}